.inputContainer{background-color:#000}.inputContainer textarea{width:80%;margin:0 auto;box-sizing:border-box;display:block;border:none;background-color:#000;color:#FAFAFA;font-family:"Source Code Pro",Courier New,Courier,monospace;resize:none;outline:none;min-height:300px;max-height:100vh;font-size:1em}@media screen and (max-width: 800px){.inputContainer textarea{width:calc(100% - 4em);margin:0 2em}}.inputContainer textarea::placeholder{color:#FAFAFA}.results{width:80%;margin:0 auto;box-sizing:border-box;border-collapse:collapse;text-align:left;margin-top:2em}@media screen and (max-width: 800px){.results{width:calc(100% - 4em);margin:0 2em}}.results tr:first-child,.results tr:not(:last-child){border-bottom:1px solid #000}.results th{font-weight:bolder}.results th,.results td{padding:.5em 0;vertical-align:top}.results th:first-child,.results td:first-child{width:66%}.results a{color:#000}.results.hidden{display:none}.tsv{font-weight:bold}.topWrapper{width:80%;margin:0 auto;box-sizing:border-box;display:flex}@media screen and (max-width: 800px){.topWrapper{width:calc(100% - 4em);margin:0 2em}}.topWrapper>*{width:50%;line-height:1.5em;margin-bottom:1em;vertical-align:middle}.topWrapper>* h3{margin-bottom:0}.settings input{appearance:none;display:inline-block;width:1.5em;height:1.5em;box-sizing:border-box;margin:0 .5em -.25em 0;outline:none}.settings input[type="checkbox"]{border:2px solid #000;border-radius:2px}.settings input[type="checkbox"]:checked{background-color:#000}.settings input[type="checkbox"]:checked::after{position:absolute;content:'✓';color:#FAFAFA;font-size:1.5em;margin-top:-.25em}.settings input[type="radio"]{border:2px solid #000;border-radius:50%}.settings input[type="radio"]:checked{background-color:#000}.settings label:last-of-type{margin-left:1em}.settings input[type="text"]{outline:none;border:none;width:100%;font-family:"Source Code Pro",Courier New,Courier,monospace;font-size:1rem}.statistics{text-align:right}.invisible{color:#FAFAFA}



