@import './theme';

html, body {
	height: 100%;
	width: 100%;
	padding: 0;
	margin: 0;
	background: $background-color;
	font-family: $font;
	font-weight: 400;
	color: #444;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

* {
	box-sizing: border-box;
}

#app {
	height: 100%;
}



